import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import * as moment from 'moment';
import { Chart } from 'angular-highcharts';
import { ToastController } from '@ionic/angular';
import {DatapointsService} from '../../services/datapoints/datapoints.service';
import {AuthenticationService} from '../../services/authentication/authentication.service';

@Component({
    selector: 'app-datapoint',
    templateUrl: './datapoint.page.html',
    styleUrls: ['./datapoint.page.scss'],
})
export class DatapointPage implements OnInit {

    public datapoint:any;
    public value:any;
    public values:Array<any>;
    
    public chart = new Chart({
        chart: {
            type: 'line',
            reflow:true,
            height:200,
            marginLeft:40      
        },
        title: {
            text: undefined
        },
        credits: {
            enabled: false
        },
        xAxis: {
            type: 'datetime'
        },    
        yAxis: {
            title: {
                text:null
            }
        },
        legend: {
            enabled:false  
        },
        series: []
    });   
    
    public inCooldown:boolean; 
    

    constructor(private route:ActivatedRoute, private datapointsService:DatapointsService,public toastController: ToastController, 
        private authenticationService: AuthenticationService, private router: Router) { 
        
        let datapointId = this.route.snapshot.params['id'];   
        this.datapoint = {timeSeriesId:datapointId, value:0};
        
        this.value = {};
        this.values = [];
        
        this.getDatapoint();
        
        
        this.inCooldown = false;
        
        
        setTimeout(() => {
            this.getDatapointValues();
        }, 300000);         
        
        
    }

    ngOnInit() {
    }
    
    
    public getDatapoint(){
        this.datapointsService.getDatapoint(this.datapoint.timeSeriesId).then((data:any) => {
            this.datapoint.timeSeriesName = data.timeSeriesName;
            this.getDatapointValues();
        }).catch(() => {
            this.datapointsError();
        })
    }
    
    async datapointsError() {
        const toast = await this.toastController.create({
            message: 'Error retreiving data',
            duration: 4000,
            color:'danger',
            mode:'md',
            buttons: []
        });
        toast.present();
    }     
    
    
    public add(){
        
        if (this.inCooldown){
            this.inCooldownError();
            return;
        }
        
        this.inCooldown = true;
        setTimeout(() => {
            this.inCooldown = false;
        },1000);
        
        
        let timeout = setTimeout(() => {
        
            this.datapointsService.addValue(this.datapoint, {value: 1}).then((data:any) => {



                //this.getDatapointValues();

                this.datapointsService.datapointsChanged(this.datapoint.timeSeriesId);

            }).catch((e) => {
                if (e.status === 401){
                    this.refreshTokenAdd();
                }
                else{ 
                    this.datapoint.value -= 1;
                    this.getDatapointValues();                    
                    this.valueAddedError();           
                }
            }); 
        
        },10000); 
        
        
        this.datapoint.value += 1;
        this.datapoint.last_added = new Date();

        this.values.push({value:this.datapoint.value, date: this.datapoint.last_added});

        this.valueAdded(timeout);

        this.setGraph();        
        
        
    }
    

    
    
    
    public refreshTokenAdd(){
        
        this.authenticationService.refreshToken().then(() => {
            
            this.datapointsService.addValue(this.datapoint, {value: 1}).then((data:any) => {
                //datapoint.value += 1;
                //datapoint.last_added = new Date();
                //this.valueAdded(datapoint);
            }).catch(() => {
                this.valueAddedError(); 
                this.datapoint.value -= 1;
                this.getDatapointValues();                
            })
            
        }).catch(() => {
        
            this.valueAddedError(); 
            this.router.navigate(['login']);
        });         
        
       
    }    
    
    
    
    
    
    async valueAdded(timeout) {
        const toast = await this.toastController.create({
            message: 'Changes Saved',
            duration: 10000,
            color:'dark',
            mode:'md',
            buttons: [
            
                {
                    side: 'end',
                    text: 'Undo',
                    handler: () => {
                        clearTimeout(timeout);
                        this.datapoint.value -= 1;
                        this.getDatapointValues();
                                              
                    }
                }
                
            ]
        });
        toast.present();
    }    
    
    async valueAddedError() {
        const toast = await this.toastController.create({
            message: 'Error adding value. Try again soon.',
            duration: 2000,
            color:'danger',
            mode:'md',
            buttons: []
        });
        toast.present();
    }     
    
    async inCooldownError() {
        const toast = await this.toastController.create({
            message: 'Wait 1 second to add value again',
            duration: 1000,
            color:'warning',
            mode:'md',
            buttons: []
        });
        toast.present();
    }    
    
    
    public setAmount(){
        this.datapoint.value += this.value.value;
        this.value.loading = true;
        this.datapointsService.addValue(this.datapoint, {value: this.value.value}).then((data:any) => {

            this.datapoint.last_added = new Date();
            this.datapoint.value = data.value;
            
            this.values.push({value:data.value, date:new Date()});
            
            this.value.value = "";
            this.value.date = "";
            this.value.time = "";
            
            this.value.loading = false;
            
            this.getDatapointValues();
            
            this.datapointsService.datapointsChanged(this.datapoint.timeSeriesId);
            
            
            
            //this.valueAdded();
        }).catch(() => {
            this.value.loading = false;
        });         
    }
    

    
    private getDatapointValues(){
        this.datapointsService.getValues(this.datapoint).then((data:Array<any>) => {

            this.values = data;
            
            if (data && data.length > 0){
                this.datapoint.last_added = data[data.length - 1].date;

                let value = 0;

                for (let valueEntry of data){
                    value += 1 /*valueEntry.value */;
                }
                
                this.datapoint.value = value;
                
                this.setGraph();

            }
            else{
                this.datapoint.value = 0;                   
            }            
            
            
            
        })  
    }
    
    private setGraph(){
        
        let series = [];

        let valueTotal = 1;
        for (let value of this.values){
            series.push([new Date(value.date).getTime(), valueTotal]);
            valueTotal += 1;
        }

             
        this.chart.removeSeries(0);
        this.chart.addSeries({
            name: 'Value',
            color: "#FF015B",
            data: series,
            type: undefined
        },true,true);
        
        this.chart.ref.reflow();
        
        
    }
    
    public getIncreaseToday(){
        let today = moment();
        let total = 0;
        for (let value of this.values){
            if (moment(value.date).isSame(today, "day")){
                total += 1;
            }
        }
        
        return total;
        
    }
    
}
